/* Gilroy-regular - latin */
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 400;
  src: local("Gilroy"), url("../fonts/Gilroy-regular.woff2") format("woff2"), url("../fonts/Gilroy-regular.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none;
  color: #000000; }

a:visited {
  color: #000000; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  width: 100%;
  height: auto; }

.hidden {
  display: none; }

body {
  font-family: "Gilroy", "Arial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  font-weight: 300;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #000000;
  background-color: #e5e5e5; }

.include {
  display: none; }

.main {
  flex-grow: 1; }

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 10px; }

@media (max-width: 1023px) {
  .container {
    max-width: 1023px; } }

@media (max-width: 767px) {
  .container {
    max-width: 767px; } }

