.navigation {
  padding-top: 7.5px;
  padding-bottom: 30px;
  background-color: #ffffff; }

.navigation__inner {
  display: flex;
  flex-direction: column;
  max-width: 1300px; }

.navigation__upper-part {
  display: flex;
  justify-content: space-around;
  position: relative;
  margin-bottom: 16px; }

.navigation__toggle-mobile {
  display: none; }

.navigation__toggle {
  min-width: 180px;
  background-color: #4496f6;
  padding-top: 10px;
  padding-bottom: 9px;
  border: none;
  color: #ffffff;
  font-size: 16px;
  line-height: 19.09px;
  font-weight: 600;
  max-height: 38px;
  border-radius: 5px;
  margin-top: 7px;
  cursor: pointer; }

.navigation__toggle:hover,
.navigation__toggle:focus {
  opacity: 0.5; }

.navigation__input {
  min-width: 734px;
  max-height: 38px;
  margin-top: 7px;
  padding-left: 86px;
  border-radius: 8px;
  background-color: #fafafa;
  border: 1px solid rgba(52, 125, 184, 0.15);
  margin-right: 30px; }

